.root{
    position: relative;
    border-bottom: 0.01rem solid #ccc;
    padding-left:0.6rem;
    overflow-x: hidden;
}
.root:last-child{
    border: none;
}
.toggle{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    text-align:center;
    margin: 0 auto;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: none;
    outline: none
}
.toggle:after{
    display: block;
    width: 0.7rem;
    height: 100%;
    content: '';
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 -18 100 136"><circle cx="50" cy="50" r="50" fill="none" stroke="#ccc" stroke-width="4"/></svg>') center no-repeat;
    background-size: 70% 70%;
}
.toggle:checked:after{
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 -18 100 136"><circle cx="50" cy="50" r="50" fill="none" stroke="#1abc9c" stroke-width="4"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>') center center no-repeat;
    background-size: 70% 70%;
}
.completed{
    opacity: 0.5
}
.text{
    display: block;
    font-size: 0.3rem;
    padding: 0.25rem 0.6rem 0.25rem 0.25rem;
    line-height: 1;
}
.content{
    position: relative;
    opacity: 1;
    transition: opacity 200ms ease-out;
    line-height: 1;
    cursor: pointer;
    text-transform: capitalize
}
.content:after{
    position: absolute;
    display: block;
    content: '';
    width: calc(100% + 1em);
    height: 0.02rem;
    top: 50%;
    left: -0.5em;
    background: #2e2e2e;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: center right;
            transform-origin: center right;
    transition: -webkit-transform 500ms cubic-bezier(0.39, 0.575, 0.565, 1);
    transition: transform 500ms cubic-bezier(0.39, 0.575, 0.565, 1);
    transition: transform 500ms cubic-bezier(0.39, 0.575, 0.565, 1), -webkit-transform 500ms cubic-bezier(0.39, 0.575, 0.565, 1);
}
.contentCompleted:after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: center left;
            transform-origin: center left;
}
.btns{
    display: -ms-flexbox;
    display: -webkit-flex;
    display: -webkit-box;
    display: flex;
    -ms-flex-flow: row-reverse nowrap;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
            flex-flow: row-reverse nowrap;
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.deleteBtn, .editorBtn, .cancelBtn{
    border: none;
    outline: none;
    padding: 0 0.3rem;
    color: #f5f5f5;
}
.deleteBtn{
    background: #ff6666;
}
.editorBtn{
    background: #66cc99;
}
.cancelBtn{
    background: #999999;
}


