.root{
    width:100%;
    text-align:center;
    margin-top: 0.1rem;
}
.filters{
    display:-ms-flexbox;
    display:-webkit-flex;
    display:-webkit-box;
    display:flex;
    -ms-flex-flow: row nowrap;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            flex-flow: row nowrap;
    -ms-flex-pack: justify;
        -webkit-box-pack: justify;
            justify-content: space-between;
    -ms-flex-align:center;
        -webkit-box-align:center;
            align-items:center;
    padding: 0.2rem 0;
}
.count{
    display: block;
    text-align: left;
    color:#999;
}
.selected{
    color: #1abc9c;
}
.clearBtn{
    padding: 0.2rem 0.8rem;
    border: 0.01rem solid #1abc9c;
    border-radius: 0.1rem;
    outline: none;
    background: #1abc9c;
    color: #fff;
    line-height: 1;
}
.clearBtn[disabled]{
    background: #ddd;
    border-color: #ddd;
    color:#666;
    cursor:not-allowed;
}


